.Chat {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 95vh;
}

.Chat-header {
    border-radius: 10px;
    background-color: #008b8b;
    overflow: visible;
    width: 100%;
    text-align: center;
    color: #f0f8ff;
    margin-bottom: 10px;
    user-select: none;
}

.Messages-list {
    padding: 20px 0;
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    list-style: none;
    flex-grow: 1;
    overflow: auto;
}

.Messages-message {
    display: flex;
    margin-top: 10px;
}

.Messages-message.currentMember {
    /*justify-content: flex-end;*/
    flex-direction: row-reverse;
    text-align: right;
}

.Message-content {
    display: inline-block;
}

.currentMember > .Message-content {
    align-items: flex-end;
}

.Messages-message > .avatar {
    height: 35px;
    width: 35px;
    border-radius: 50%;
    display: inline-block;
    margin: 0 10px -10px;
}

.Message-content > .username {
    display: block;
    color: gray;
    font-size: 14px;
    padding-bottom: 4px;
}

.Message-content > .text {
    padding: 10px;
    max-width: 400px;
    margin: 0;
    border-radius: 12px;
    background-color: cornflowerblue;
    color: white;
    display: inline-block;
    word-wrap: break-word;
}

.currentMember > .Message-content .text {
    /*background-color: orangered;*/
}

form.chat_form {
    display: flex;
    width: 100%;
    justify-content: space-between;
    max-width: 900px;
    margin: 0 auto 40px;
}

input.chat_input {
    padding: 5px;
    font-size: 16px;
    border-radius: 8px;
    border: 1px solid #008b8b;
    flex-grow: 1;
}

button.chat_button {
    padding: 5px 10px;
    font-size: 16px;
    background-color: #008b8b;
    color: white;
    border: none;
    border-radius: 8px;
    margin-left: 10px;
    user-select: none;
}

.Input {
    margin-top: 20px;
}