body {font-family: Arial, Helvetica, sans-serif;}
html{
    min-height:100%;
    background-size:cover;
}
body{
    min-height:100%;
}

input[type=text], input[type=password] {
    width: 60vh;
    padding: 2vh 2vh;
    margin: 0px 0;
    font-size: 2vh;
    display: inline-block;
    box-sizing: border-box;
}
/* Общие стили кнопок */
.button1 {
    color: white;
    height: 100%;
    margin: 5px 0;
    cursor: pointer;
    font-size: 3.6vh;
    border: black;
    width: 100%;
    height: 10vh;
}

.button1:hover {
    opacity: 1;
}
/* корбалик внутри формы */
.imgcontainer {
    text-align: center;
    margin: 1vh 0 1vh 0;
    position: relative;
}

img.avatar {
    width: 20vh;
}

.container {
    padding: 1vh;
}

span.psw {
    float: right;
    padding-top: 3vh;
}
/* Сама форма */
.modal {
    display: none;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    min-height:100%;
    background-size:cover;
    overflow: auto;
}
.modal:target {
    opacity: 1;
    pointer-events: auto;
    overflow-y: auto;
}

.modal-content {
    background-color: #ffffff;
    margin: 10% auto 10% auto;
    border: 2px solid black;
    box-sizing: border-box;
    width: 62vh;
    opacity: 1;
}
@media (min-width: 768px) {
    .modal-content {
        -webkit-box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
        box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
    }
}
/* X - закрытие формы */
.close {
    position: absolute;
    right:3vh;
    top: 0;
    margin: auto;
    color: rgb(0, 0, 0);;
    font-size: 7vh;
    font-weight: bold;
}

.close:hover,
.close:focus {
    color: red;
    cursor: pointer;
}
/* Анимация к форме */
.animate {
    -webkit-animation: animatezoom 2s;
    animation: animatezoom 2s
}

@-webkit-keyframes animatezoom {
    from {-webkit-transform: scale(0)}
    to {-webkit-transform: scale(1)}
}

@keyframes animatezoom {
    from {transform: scale(0)}
    to {transform: scale(1)}
}


@media screen and (max-width: 300px) {
    span.psw {
        display: block;
        float: none;
    }
    /* Кнопка входа в игру */
}
.button1{
    background: rgba(2, 19, 31, 0.9);
}
