/* Кнопка открытия формы для авторизации */
.ENT {
    text-decoration: none;
    outline: none;
    text-align: center;
    position: absolute;
    margin-top: -5vh;
    top: 50%;
    background: rgba(255, 255, 255, 0);
    width: 55vh;
    height: 10vh;
    margin-left: -25vh;
    left: 50%;
    display: block;
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.4);
    background: rgba(0, 0, 0, 0.7);
    font-size: 2.6vh;
    font-family: Lobster, cursive;
    text-transform: uppercase;
}
.ENT:before,
.ENT:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    box-sizing: border-box;
}
.ENT:before {
    bottom: 0;
    left: 0;
    border-left: 1px solid white;
    border-top: 1px solid white;
    transition: width .2s ease .4s, height .2s ease .6s;
}
.ENT:after {
    top: 0;
    right: 0;

    border-right: 1px solid white;
    border-bottom: 1px solid white;
    transition: 0s  ease width , .2s ease height .2s;
}
.ENT:hover:before,
.ENT:hover:after{
    height: 100%;
    width: 100%;
}
.ENT:hover:before {transition: .2s ease height, .2s ease width .2s;}
.ENT:hover:after {transition: height .2s ease .4s , width .2s ease .6s;}
.ENT:hover {background: rgba(2, 19, 31, 0.9);}